  .p-our-story .c-box-time-line__item:nth-child(even) .c-box-time-line__content {
    order: 2;
  }
}

@media (max-width: 896px) {
  .p-our-story .c-box-time-line__item::before {
    content: '';
    width: 5px;
    height: 100%;
    position: absolute;
    border-left: 5px dotted #DCDCDC;
