    width: 120px;
    height: 120px;
  }
}

.p-our-story .c-box-time-line__image .c-deco-story {
  width: 941px;
  height: 778px;
  position: absolute;
  right: 0;
  top: -80px;
  background: url(/assets/img/our-story/ico_deco_our_story_01.svg) no-repeat center center;
  background-size: cover;
  opacity: 0.3;
}

@media (min-width: 897px) and (max-width: 1439px) {
  .p-our-story .c-box-time-line__image .c-deco-story {
    width: 65.34722vw;
    height: 54.02778vw;
    top: -5.55556vw;
  }
}

@media (max-width: 896px) {
  .p-our-story .c-box-time-line__image .c-deco-story {
